export const show = (path, timestamp, options) => {
  const url = new URL(`${document.location.origin}/api/show`);
  url.search = new URLSearchParams(Object.assign(timestamp ? { timestamp } : {}, options));

  return fetch(url, {
    method: 'POST',
    body: JSON.stringify({ path }),
    headers: {
      'Content-Type': 'application/json'
    }
  }).then(response => {
    // console.log(response.blob());

    return response.blob();
  });
};
